<template>
  <v-app>
    <Drawer></Drawer>
    <v-main>
      <h1 class="text-center">Tandas</h1>
      <v-container>
        <v-row justify="center" v-for="(item, index) in tandas" :key="index">
          <v-col cols="12" sm="4">
            <v-card elevation="4" class="rounded-lg mx-auto">
              <v-img max-height="200" contain :src="item['portada']"></v-img>
              <v-card-title>{{ item["title"] }}</v-card-title>
              <v-card-text>
                <div>
                  {{ item["description"] }}
                </div>
              </v-card-text>
              <v-divider></v-divider>
              <v-card-actions>
                <v-btn fab small color="accent">
                  <v-icon color="white">mdi-pencil</v-icon>
                </v-btn>
                <v-btn fab small color="red" @click="showDialoagDeleteF(item)">
                  <v-icon color="white">mdi-delete</v-icon>
                </v-btn>
              </v-card-actions>
            </v-card>
          </v-col>
        </v-row>
      </v-container>
    </v-main>
    <DialogAdd :loadTandas="loadTandas"></DialogAdd>
    <!-- Dialog delete -->
    <div class="text-center">
      <v-dialog width="400" v-model="showDialogDelete">
        <v-card :disabled="requestDelete" :loading="requestDelete">
          <v-card-title class="text-h5"> Desea Eliminar? </v-card-title>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="error" @click="dialogDeleted()"> Eliminar </v-btn>
            <v-btn color="primary" @click="showDialogDelete = false">
              Cancelar
            </v-btn>
          </v-card-actions></v-card
        ></v-dialog
      >
    </div>
  </v-app>
</template>

<script>
import Drawer from "../components/Drawer";
import DialogAdd from "../components/Tandas/DialogAdd";
import axios from "../services/axios";

export default {
  data: () => {
    return {
      showDialogDelete: false,
      requestDelete: false,
      itemSelected: null,
      tandas: [],
    };
  },
  methods: {
    showDialoagDeleteF(item) {
      this.showDialogDelete = !this.showDialogDelete;
      this.itemSelected = item;
    },
    dialogDeleted() {
      this.requestDelete = true;
      axios.delete("/api/tandas/" + this.itemSelected["id"]).then((res) => {
        this.requestDelete = false;
        this.showDialogDelete = false;
        this.loadTandas();
      });
    },
    loadTandas() {
      axios.get("/api/tandas").then((res) => {
        if (res.status == 200) {
          this.tandas = res.data;
        }
      });
    },
  },
  created() {
    this.loadTandas();
  },
  components: {
    Drawer,
    DialogAdd,
  },
};
</script>